<template>
  <div>
    <div
      style="width:100%;
          height:38px;
          background:rgba(13,67,111,1);
          box-shadow:0px 2px 10px 0px rgba(0, 0, 0, 0.05);
          font-size:14px;
          font-family:Microsoft YaHei;
          font-weight:400;
          color:rgba(255,255,255,1);
          padding-left:24px;
          line-height:38px;"
    >
      CDN监控业务
    </div>
      <!-- <div style="text-align:center;height:450px;line-height:450px;font-size:20px;"> -->
      <div>
        <!-- <Button type="primary">点击按钮，跳转到CDN监控系统界面</Button> -->
        <!-- <a href="https://www.baidu.com/">点击链接，跳转到CDN监控系统界面。</a> -->
        <!-- <iframe src="https://www.baidu.com/"  style="width:100%;heigth:600px;"></iframe> -->
        <!-- <iframe :src="cdnMonitorUrl"  style="width:100%;height:600px;"></iframe> -->

        <iframe :src="cdnMonitorUrl" style="width:100%;" :style="{ height:tabHeight + 'px' }"></iframe>
        <!-- <iframe src="http://192.166.66.242/zabbix" style="width:100%;" :style="{ height:tabHeight + 'px' }"></iframe> -->
      </div>  
  </div>
</template>
<script>
import api from '@/api/api';

export default {
  data(){
    return {
      cdnMonitorUrl:'', //cdn监控页面地址
      base:1,   // 分辨率自适应 计算后的倍数
      tabHeight:0,  // 分辨率自适应调整：表格的高度
    }
  },
  created(){
    this.getCdnMonitorUrl();
  },
  mounted() {
    this.autoFix();
  },
  methods:{

    /** 自适应 */
    autoFix(){
      let vm = this;
      this.getDom();
      window.onresize = function(){
        vm.getDom();
      }
    },

    /** 获取窗口的宽和高 */
    getDom(){
      let width = window.innerWidth;
      let height = window.innerHeight;
      this.base = (width-160)/(1024-160);
      // this.tabHeight = height - 350;  // 表格页面有“过滤搜索条件”card，表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      // this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      this.tabHeight = height - 120;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
    },

    getCdnMonitorUrl(){
      let param = {}

      api.getPublicConfig(param).then(
        res => {
          console.log(res.result);
          this.cdnMonitorUrl = res.result.cdnMonitorUrl;
        },
        err => {
          this.$Message.error("访问失败！");
        }
      )
    }
  }
}
</script>
